import HomeScreen from "./components/HomeScreen.vue";
import HelpScreen from "./components/HelpScreen.vue";
import ThankyouScreen from "./components/ThankyouScreen.vue";
import ProductListScreen from "./components/ProductListScreen.vue";
import ProductDetailScreen from "./components/ProductDetailScreen.vue";
import PouringScreen from "./components/PouringScreen.vue";
import { BluetoothTerminal } from './assets/js/BluetoothTerminal.js';
import { mapActions } from "vuex";

export default {
    name: "app",
    components: {
        HomeScreen,
        HelpScreen,
        ThankyouScreen,
        ProductListScreen,
        ProductDetailScreen,
        PouringScreen
    },
    data: function () {
        return {
            terminal: null,
            deviceId: 0,
            rfIdNumber: 0,
            taps: [],
            deviceDetails: {},
            userDetails: {},           
            productDetails: {},
            currentPage: 1,
            lastPage: 1,
            currentUserBalance: 0
        }
    },
    mounted() {

    },
    methods: {
        ...mapActions(["getModule","getUserDetails", "saveConsumerHistory"]),
        init() {
            // Obtain configured instance.
            this.terminal = new BluetoothTerminal('6e400001-b5a3-f393-e0a9-e50e24dcca9e', '6e400002-b5a3-f393-e0a9-e50e24dcca9e');
            this.connectToDevice();
            this.terminal.receive = function (command, data) {
                this.receiveData(command, data);
            };
            this.terminal._log = function(...messages) {
                // We can't use `super._log()` here.
                messages.forEach((message) => {                 
                  console.log(message); // eslint-disable-line no-console
                });
              };              
        },
        changePage(lastPage , page){
            this.lastPage = lastPage;
            this.currentPage = page;
        },
        goToBack(){
            this.currentPage = this.lastPage;
        },
        goToProductDetails(product, tapNumber){
          this.productDetails = product;
          this.productDetails.TapNumber = tapNumber;          
        },
        serve(){
          this.terminal._startPour();
        },
        receiveData(command, data) {
            if (command == 21) {
                this.deviceId = data;
                if (this.checkDevice()) {
                    this.terminal._sendToDevice(command, 1);
                } else {
                  this.terminal._sendToDevice(command, 0);
                }
              } else if (command == 31) {
                this.rfIdNumber = data;
                if (!this.checkRFIDExists()) {
                    this.currentPage = 4;
                    this.terminal._sendToDevice(command, 0);
                }
              } else if (command == 41) {
                //Tap Number 1-Left 2-Right
                var tapNumber = this.productDetails.TapNumber;
                this.terminal._sendToDevice(command, tapNumber);
              } else if (command == 42) {
                //Volume of Tap in ml
                var volume = this.productDetails.Volume;
                volume = volume * 1000;
                this.terminal._sendToDevice(command, volume);
              } else if (command == 43) {
                //Beer price in cent
                var price = this.productDetails.Product.Price;
                price = price * 100;
                this.terminal._sendToDevice(command, price);
              } else if (command == 44) {
                //User Amount in cent
                var userBalance = this.userDetails.CardCustomer[0].Amount;
                userBalance = userBalance * 100;
                this.terminal._sendToDevice(command, userBalance);
              } else if (command == 32) {
                //Finish Pouring
                //var lastAmount = data;
                this.saveHistory(data);
              } else if (command == 33) {
                //Finish Pouring
                this.saveHistory(data);
              } else if (command == 34) {
                //Finish Pouring
                this.saveHistory(data);
              } else if (command == 35) {
                //Finish Pouring
                this.saveHistory(data);
              } else if (command == 36) {
                //Finish Pouring
                //var lastAmount1 = data;
                this.currentUserBalance = data;
              } else if (command == 51) {
                //Finish Pouring
                //var lastAmount = data;
                this.saveHistory(data);
              }
        },
        connectToDevice() {
            this.terminal.connect().
            then(() => {
                console.log(this.terminal.getDeviceName() ? this.terminal.getDeviceName() : "Default Device")
            });
        },
        checkRFIDExists(){
            this.getUserDetails(this.rfIdNumber).then( response => {
                if(!response.error){
                    this.userDetails = response.data;
                    this.currentUserBalance = this.userDetails.CardCustomer[0].Amount;
                    return true;
                }  
                return false;                           
              }, 
              error => {
                return false;
              });
        },
        checkDevice(){
            this.getModule(this.deviceId).then( response => {
                if(!response.error){                  
                    this.deviceDetails = response.data;
                    return true;
                }                             
              }, 
              error => {
                return false;
              });
        },
        saveHistory(data){
           setTimeout(function(){ 
                this.currentPage = 3;
            }, 1000);       
            this.currentPage = 1; 
            var price = this.productDetails.Price;          
            var quantity = 0;
            quantity = (this.currentUserBalance) / (price * 1000);
            quantity = quantity / 1000;
            var model = {
                User: JSON.stringify(this.userDetails.CardCustomer[0].User),
                Id_Card: this.userDetails.Id_Card,
                Type: "Consumption",
                Amount: this.currentUserBalance,
                Quantity: quantity,
                Product: JSON.stringify(this.productDetails.Product),
                Id_Tap: this.productDetails.Id_Tap,
                Id_Location: this.deviceDetails.Id_Location,		            
                Id_Module: this.deviceDetails.Id_Module,
                Id_Totem:this.deviceDetails.Id_Totem
            };    
            this.saveConsumerHistory(model).then( response => {
                if(!response.error){
                  this.clearData();
                }                             
              }, 
              error => {
                
              });
        },
        clearData(){
          this.userDetails = {};
          this.rfIdNumber =  0;         
          this.productDetails = {};
        },
        startPouring(){
            this.terminal._startPour();            
        },
        tapStop(){
            this.terminal._tapClosed();
        }
    }
};